import React, { memo, useRef, useState } from "react";
import styled from "styled-components";
import TimeAgo from "timeago-react";
// import { useExportPdf } from "../../common/hooks/useExportPdf";
import { usePdf } from "../../common/hooks/usePdf";
import usePdfExport from "../../common/hooks/usePdfExport";
import { config } from "../../config/api";
import ExportPdfModal from "./ExportPdfModal";
import HashLoader from "react-spinners/HashLoader";
import Pagination from "../../common/components/Pagination";
import { m } from "framer-motion";
import useModal from "../../common/hooks/useModal";
import { useSelector } from "react-redux";

const PdfManagement = ({ className }) => {
  const { open } = useModal("PDF_EXPORT");
  const error = useSelector((state) => state.error?.export_pdf);
  const [page, setPage] = useState(1);
  const { data: pdfData } = usePdf({ page });
  const { last_page, data } = pdfData || {};
  const [modalOpen, setModalOpen] = useState(false);
  const list = useRef(null);

  // const queryClient = useQueryClient()
  const { mutate: exportPdf, isLoading } = usePdfExport();
  const handleExport = async () => {
    if (isLoading) return;
    exportPdf();
    setModalOpen(true);
  };

  return (
    <section
      className={`${className} user__management__export col-12 col-md-10`}
    >
      <header className="fs__title fw-bolder">Xuất file PDF ERA của bạn</header>
      <p className="fs__text my-3">
        Lorem ipsum dolor sit amet consectetur adipisicing elit. Ipsam facere
        deserunt illum repellat. Quo enim tenetur quaerat. Perspiciatis
        voluptatum nihil ratione illum cumque doloremque et dolor sed expedita?
        Qui, pariatur? Lorem ipsum dolor sit amet consectetur adipisicing elit.
        Voluptas doloremque beatae possimus, laboriosam eveniet quo nesciunt et
        deleniti facere vero!
      </p>

      <m.div
        className={`button__export button-secondary ${
          isLoading ? "disabled" : ""
        }`}
      >
        <div onClick={open}>
          {isLoading ? "Đang xuất pdf" : "Xuất file pdf"}
        </div>
        &nbsp;
        <HashLoader color="#fafafa" size={24} loading={isLoading} />
      </m.div>
      <div className="error mt-2">{error}</div>
      <header className="fs__title fw-bolder mt-4">
        Danh sách file pdf của bạn
      </header>
      <section className="pdf__list d-flex flex-column my-4">
        {data?.map((pdf) => {
          return (
            <div
              key={pdf.id_p}
              className="pdf__item d-flex justify-content-between p-3 button-secondary--outline"
            >
              <a
                target="_blank"
                href={`${config.apiResource}${pdf.link_p}`}
                rel="noreferrer"
              >
                {pdf.name_p}
              </a>
              <TimeAgo datetime={pdf.created_at} locale="vi" />
            </div>
          );
        })}
      </section>
      {pdfData && (
        <Pagination
          currentPage={page}
          pageCount={last_page}
          initialPage={page}
          onPageChange={setPage}
          list={list}
        />
      )}
      <ExportPdfModal isOpen={modalOpen} setOpen={setModalOpen} />
    </section>
  );
};

export default styled(PdfManagement)`
  &.user__management {
    &__export {
      .pdf {
        &__list {
          row-gap: 12px;
        }
        &__item {
          border-radius: 12px;
          a {
            text-transform: capitalize;
            font-weight: bold;
            color: inherit;
          }
        }
      }
      .button__export {
        display: inline-flex;
        padding: 10px 15px;
        /* background-color: ${(props) => props.theme.colorSecondary}; */
        font-weight: bolder;
        border-radius: 12px;
        div {
          cursor: pointer;
          /* color: #fafafa; */
        }
      }
    }
  }
`;
